import { ColorProps } from '../UNSAFE_interpolations/colors';

export type DvtThresholdsColors = 'danger' | 'warning' | 'success';
import { colorSchemeVars } from '../../Common/themes/themeContract.css';

/**
 * Returns the color of the threshold.
 * @param color The color provided via prop of the dvt component.
 * @returns The color of the threshold.
 */
export function getDvtThresholdColor(color: ColorProps['color'] | DvtThresholdsColors) {
  const colorAsString: string = color as string;
  if (colorAsString === 'danger' || colorAsString === 'warning' || colorAsString === 'success') {
    return colorSchemeVars.dvt.threshold[colorAsString];
  }
  return color;
}
